import React from 'react';

import DynamicToggles from './DynamicToggles';
import TimeStamp from './TimeStamp';
import DivButtonTest from './DivButtonTest';


import './Notebook.css';

const Notebook = () => {
    return(
        <div>
            <div className="NotebookTitle">
                Notebook
            </div>
            <hr/>

            <div className="NotebookEntry">
                <DynamicToggles />
            </div>
            <hr />

            <div className="NotebookEntry">
                <TimeStamp />
            </div>
            <hr />

            <div className="NotebookEntry">
                <DivButtonTest />
            </div>
            
        </div>
    )
}

export default Notebook;