import React from 'react';

import './TimeStamp.css';

const TimeStamp = () => {
   
    const currentDate = new Date();

    return(
        <div className="Entry">
            <div className="EntryTitle">
                <h2>Timestamps</h2>
            </div>
            <br />
            <div className="EntryPurpose">
                <b>Purpose: </b>
                <span>To learn about the specific calls in JS to create a useable timestamp.</span>
                <span>These timestamps will be used (probably as object keys in Firebase.</span>
            </div>
            <br />
            <div className="EntryReadme">
                <b>Use: </b>
                <span>Write this later.</span>
            </div>
            <br />
            
            <div className="EntryData">
                <div className="EntryRow"><b>Date:    </b> {Date()} </div>
                <div className="EntryRow"><b>Year:    </b> {currentDate.getFullYear()}</div>
                <div className="EntryRow"><b>Month:   </b> {currentDate.getMonth()}</div>
                <div className="EntryRow"><b>Day:     </b> {currentDate.getDay()}</div>
                <div className="EntryRow"><b>Hours:   </b> {currentDate.getHours()}</div>
                <div className="EntryRow"><b>Minutes: </b> {currentDate.getMinutes()}</div>
                <div className="EntryRow"><b>Seconds: </b> {currentDate.getSeconds()}</div>
            </div>
        </div>
    );
};

export default TimeStamp;