import React from 'react';
import { Accordion } from 'react-bootstrap';


import './HomeworkItem.css';
import '../../../assets/fonts/ProductSans/Product Sans Regular.ttf';

const HomeworkItem = (props) => {

    const homeworkData = props.data;
    const hwDataKeys = Object.keys(homeworkData);


    const convertDate = (str) => {
        let titleString = '';
        let months = ["January","February","March","April","May","June","July","August","September","October","November","December"]
        let year = str.slice(0,4)
        let month = str.slice(5,7);
        let day = str.slice(8,10)
        
        let textMonth = months[parseInt(month)-1]
        
        titleString = year + ' ' + textMonth + ' ' + day;
        return(titleString)
    }

    // console.log(Object.keys(homeworkData));

    return(
        <div className='HwAccordionElement'>
            <Accordion>
                <Accordion.Item eventKey='1'>
                    <Accordion.Header>
                        <div className="HomeworkDate">
                            {convertDate(props.title)}
                        </div>                    
                    </Accordion.Header>
                    <Accordion.Body>
                        {/* <div dangerouslySetInnerHTML={{
                            __html: props.data
                        }} /> */}
                        {hwDataKeys.map((hwKey, idx)=>(
                            <div className="HomeworkDetails" key={idx}>
                                {/* <div className=""> */}
                                    <ul>
                                        <li>{homeworkData[hwKey].HomeworkItem}</li>
                                        {/* <li type="none" style={{ textIndent: "50px" }}>{homeworkData[hwKey].Description}</li> */}
                                        <li type="none" style={{ paddingLeft: "85px" }}>{homeworkData[hwKey].Description}</li>
                                    </ul>
                                {/* </div> */}
                            </div>
                        ))

                        }

                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            {/* <Card>
                -----
            </Card> */}
        </div>
    )
}

export default HomeworkItem;